.order-modal-container {
  display: none;
}

.show-order-modal-container {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}

.order-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 80%;
  max-width: 800px;
  max-height: 500px;
  background-color: #4d4d4d;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 0.25rem;
}

.order-title {
  flex: 0 0 64px;
  /* padding: 0.125rem 0.5rem; */
  padding: 0.125rem 0;
  margin-bottom: 0.35rem;
  display: flex;
  align-items: center;
  background-color: #000;
  color: yellow;
}
.order-title h2 {
  margin-left: 1rem;
  font-size: 1.6rem;
}
.order-title p {
  /* margin-left: 1.5rem; */
  margin-left: 1.5rem;
  font-size: 1.2rem;
}

.order-message {
  padding: 1rem 1rem;
  display: flex;
  align-items: center;
  background-color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
}

.order-items-list {
  flex: 1;
  overflow: auto;
  /* padding: 0.5rem 1.5rem; */
  padding: 0.25rem 1rem;
  margin-bottom: 0.35rem;
  background-color: #fff;
}
.order-item-container {
  display: flex;
  flex-direction: column;
  margin: 0.25rem 0.2rem;
  margin-bottom: 0.35rem;
  padding: 1rem 0.55rem;
  border-top: 2px solid #4d4d4d;
  border-bottom: 2px solid #4d4d4d;
}
.order-item-details {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}
.order-item-details p,
.order-item-details span {
  /* padding: 0.125rem; */
  padding: 0.175rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.order-summary {
  flex: 0 0 54px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
}
.order-total-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  padding: 0.25rem 1rem;
  align-items: center;
  font-size: 1.3rem;
  font-weight: bold;
  color: yellow;
}
.close-order-modal {
  /* flex: 0 0 100px; */
  flex: 0 0 25%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1;
  color: #000;
  background-color: #fff;
  border-color: transparent;
  border-bottom-right-radius: 0.25rem;
  cursor: pointer;
}

.close-order-modal:hover {
  /* color: #fff; */
  /* background-color: #4d4d4d; */
}
.close-order-modal .icon {
  margin-right: 10px;
  font-size: 1.4rem;
}

