    max-height: calc(100% - 90px);
    padding: 0.25rem;
  }

  .order-title {
    flex: 0 0 60px;
    /* padding: 0.125rem 0.2rem; */
    padding: 0.125rem 0;
    margin-bottom: 0.25rem;
