  }

  .order-message {
    padding: 1rem 0.5rem;
    font-size: 1.3rem;
  }
  .order-items-list {
    /* padding: 0.75rem; */
    padding: 0.2rem 0.5rem;
    margin-bottom: 0.25rem;
  }
  .order-item-container {
    /* margin: 0.15rem 0 0.3rem; */
    margin: 0.25rem 0.15rem;
    margin-bottom: 0.25rem;
    padding: 0.8rem 0.5rem;
  }
  .order-item-details p,
  .order-item-details span {
    padding: 0.125rem;
    font-size: 1rem;
  }
  .order-summary {
    flex: 0 0 50px;
  }
