.details {
  display: none;
}

.show-details {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}

.details-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 80%;
  max-width: 800px;
  max-height: 500px;
  background-color: #4d4d4d;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 0.25rem;
}

.details-title {
  flex: 0 0 64px;
  /* padding: 0.125rem 1rem; */
  padding: 0.125rem 0;
  margin-bottom: 0.35rem;
  display: flex;
  align-items: center;
  background-color: #000;
  color: yellow;
}
.details-title h2 {
  /* margin-left: 0.25rem; */
  margin-left: 1rem;
  font-size: 1.6rem;
}

.details-items-list {
  flex: 1;
  padding: 0.5rem 1.5rem;
  margin-bottom: 0.35rem;
  background-color: #fff;
  overflow: auto;
}

.details-item-container {
  display: flex;
  flex-direction: column;
  margin: 0.25rem 0.2rem;
  margin-bottom: 0.35rem;
  padding: 1rem 0.55rem;
  border-top: 2px solid #4d4d4d;
  border-bottom: 2px solid #4d4d4d;
}

.details-item-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  margin-bottom: 0.25rem;
}
.details-item-info p,
.details-item-info span {
  padding: 0.125rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.toggle-options-btn {
  margin-bottom: 0.25rem;
  padding: 0.15rem 0;
  color: #000;
  font-size: 0.95rem;
  font-weight: bold;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
}
.options-container {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0.25rem 0.5rem;
  font-weight: bold;
  font-size: 0.95rem;
  text-transform: capitalize;
  border: 1px solid #4d4d4d;
  border-radius: 0.25rem;
}

.option-item {
  display: flex;
  justify-content: space-between;
  padding: 0.175rem 0;
}

.option-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.175rem 0;
}
.details-summary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: bold;
  background-color: #000;
}

.details-total-price,
.details-total-info {
  padding: 0.35rem 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: yellow;
}

.details-total-price span,
.details-total-info span {
  margin: 0 0.35rem;
  width: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
}

.close-details {
  flex: 0 0 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1;
  background-color: #fff;
  border-color: transparent;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  color: #000;
  cursor: pointer;
}
.close-details:hover {
  /* color: #fff; */
  /* background-color: #4d4d4d; */
}
.close-details .icon {
  margin-right: 10px;
  font-size: 1.4rem;
}

