    max-height: calc(100% - 90px);
    padding: 0.25rem;
  }
  .details-title {
    flex: 0 0 60px;
    /* padding: 0.125rem 0.2rem; */
    padding: 0.125rem 0;
    /* padding-left: 0.5rem; */
