  }
  .details-item-container {
    /* margin: 0.15rem 0 0.3rem; */
    /* margin: 0.25rem 0 0.2rem; */
    margin: 0.25rem 0.15rem;
    margin-bottom: 0.25rem;
    padding: 0.8rem 0.5rem;
  }

  .details-item-info p,
  .details-item-info span {
    padding: 0.125rem;
    font-size: 1rem;
    /* border: 1px solid red; */
  }
  .toggle-options-btn {
    padding: 0.125rem;
    margin-bottom: 0.25rem;
    font-size: 0.9rem;
  }
  .options-container {
    gap: 0.2rem;
    padding: 0.25rem 0.5rem;
    font-size: 0.9rem;
  }
  .option-item {
    padding: 0.125rem 0.25rem;
  }
  .option-detail {
    padding: 0.125rem 0.25rem;
  }
  .details-total-price,
  .details-total-info {
    padding: 0.25rem 0.5rem;
  }
  .details-total-price span,
  .details-total-info span {
    width: 120px;
    padding: 0.25rem;
    font-size: 1.1rem;
  }
  .close-details {
    flex: 0 0 50px;
    font-size: 1rem;
  }
  .close-details .icon {
    margin-right: 6px;
    font-size: 1.2rem;
  }
}

