.cart {
  display: none;
}

.show-cart {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}
.cart-container {
  position: absolute;
  top: 0;
  right: 0;
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #4d4d4d;
  padding: 0 0.125rem;
  transition: all 0.5s linear;
}

.cart-title {
  flex: 0 0 80px;
  /* padding: 0.125rem 0.5rem; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  margin-bottom: 0.125rem;
  padding: 1rem;
  margin-bottom: 0.25rem;
}
.cart-title h2 {
  color: yellow;
  font-size: 1.5rem;
}

.cart-items-container::-webkit-scrollbar {
  width: 8px; /* 스크롤 너비 */
}

.cart-items-container::-webkit-scrollbar-track {
  background: #f1f1f1; /* 스크롤 트랙 배경 */
  border-radius: 10px;
}

.cart-items-container::-webkit-scrollbar-thumb {
  background: #888; /* 스크롤 thumb 색 */
  border-radius: 10px;
}

.cart-items-container::-webkit-scrollbar-thumb:hover {
  background: #555; /* 스크롤 thumb hover 시 색 */
}

.cart-items-container {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 120px;
  overflow: auto;
  background-color: #fafafa;
  padding-right: 0.125rem;
  padding: 0.3rem;
}

.cart-item {
  flex: 0 0 120px;
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  margin: 0.125rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  background-color: #e6e6e6;
}

.cart-item-header {
  flex: 0 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart-item-header h3 {
  flex: 0 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.cart-item-delete {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 58px;
  height: 32px;
  margin-right: 0.3rem;
  font-weight: bold;
  color: #fff;
  background-color: #f44336;
  border-color: transparent;
  border-radius: 0.25rem;
  transition: all 0.3s linear;
  line-height: 1;
  cursor: pointer;
}
.cart-item-delete .icon {
  font-size: 1rem;
  margin-right: 0.2rem;
}
.cart-item-delete span {
  font-size: 0.9rem;
}

