.cart-item-details {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.25rem;
}
.cart-item-quantity {
  flex: 0 70%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-weight: bold;
}

.cart-item-quantity span {
  width: auto;
  padding: 0 0.35rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.cart-item-quantity .minus,
.cart-item-quantity .plus {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.35rem;
  font-size: 1.2rem;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  color: #000;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
}

.cart-item-quantity .minus:hover,
.cart-item-quantity .plus:hover {
  border: 1px solid black;
  border-radius: 0.25rem;
}

.cart-item-price {
  flex: 0 30%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
  font-size: 1.2rem;
}
.cart-item-total {
  flex: 0 0 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.2rem;
  background-color: #fafafa;
  border-top: 1px solid #888;
}

.cart-item-total-quantity,
.cart-item-total-price {
  font-size: 1.3rem;
  font-weight: bold;
  color: #000;
}

.cart-btns {
  flex: 0 0 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000;
}
.cart-btns span {
  line-height: 1;
}
.cart-left,
.cart-right {
  height: 100%;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  border-color: transparent;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
}

.cart-left {
  flex: 0 35%;
  background-color: #b0b0b0;
  color: #000;
}

.cart-right {
  flex: 1;
  background-color: #ffeb3b;
  color: #000;
}
.cart-left .icon,
.cart-right .icon {
  margin-right: 10px;
  font-size: 1.4rem;
}

.cart-left:hover {
  background-color: #757575;
  color: #fff;
}
.cart-right:hover {
  background-color: #fbc02d;
  color: #fff;
}

